//media-queries

//max-width

@mixin mobileS {
  @media (max-width: 320px) { @content; }
}

@mixin mobileM {
  @media (max-width: 375px) { @content; }
}

@mixin mobileL {
  @media (max-width: 425px) { @content; }
}

@mixin tabletS {
  @media (max-width: 540px) { @content; }
}

@mixin tabletM {
  @media (max-width: 600px) { @content; }
}

@mixin tablet {
  @media (max-width: 768px) { @content; }
}

@mixin tabletL {
  @media (max-width: 992px) { @content; }
}

@mixin laptop {
  @media (max-width: 1024px) { @content; }
}

@mixin laptopM {
  @media (max-width: 1200px) { @content; }
}

@mixin laptopL {
  @media (max-width: 1440px) { @content; }
}

@mixin desktop {
  @media (max-width: 1920px) { @content; }
}

@mixin desktopL {
  @media (max-width: 2560px) { @content; }
}

//max-height 

@mixin MHmobileL {
  @media (max-height: 425px) { @content; }
}

@mixin MHtabletM {
  @media (max-height: 600px) { @content; }
}

//landscape
@mixin landscape {
  @media all and (orientation: landscape) { @content; }
}


//placeholder
@mixin input-placeholder {
  &.placeholder { @content; }
  &:-moz-placeholder { @content; }
  &::-moz-placeholder { @content; }
  &:-ms-input-placeholder { @content; }
  &::-webkit-input-placeholder { @content; }
}