.property-card {
  position: relative;
  border: 2px solid $accent;
  &--editing, &--deleting, &--verification {
    podition: relative;
    border: 2px solid $second-dark;
    background-color: $second;
    &:before {
      font-size: 32px;
      text-transform: uppercase;
      position: absolute;
      top: 70%;
      left: 50%;
      transform-origin: left;
      transform: translate(-50%) rotate(-20deg) ;
      z-index: 100;
      font-weight: 900;
      white-space: nowrap;
      @include laptopM {
        font-size: 20px;
      }
    }
  }
  &--editing {
    &:before {
      content: 'на редактирование';
      color: $third;
    }
  }
  &--deleting {
    &:before {
      content: 'на удаление';
      color: $alert;
    }
  }
  &--verification {
    &:before {
      content: 'на проверке';
      color: $active-dark;
    }
  }
  &__container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 10px;
    @include laptopM {
      flex-direction: column;
    }
  }
  &__img-container {
    position: relative;
    width: 208px;
    height: 208px;
    overflow: hidden;
    margin-right: 10px;
    @include laptopM {
      width: 100%;
      text-align: center;
      margin-right: 0;
    }
    @include mobileL {
      height: 180px;
    }
  }
  &__img {
    height: 100%;
    width: 100%;
    object-fit: cover;
  }
  &__link {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    z-index: 1;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      height: 100%;
      opacity: 0;
      width: 100%;
      background: $accent;
      transition: opacity .3s cubic-bezier(.4,0,.2,1);
      z-index: -1;
    }
    &:hover:after {
      opacity: .3;
    }
  }
  &__main {
    position: relative;
    width: 60%;
    display: flex;
    flex-direction: column;
    @include laptopM {
      width: 100%;
    }
    &-wrapper {
      padding: 10px;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
    &-block {
    }
  }
  &__title {
    font-size: 18px;
    margin-bottom: 15px;
    color: $accent;
  }
  &__descr {
    height: 100%;
    font-weight: 300;
    margin-bottom: 20px;
  }
  &__info {
    &-row {
      display: inline-block;
      margin-right: 10px;
      &:last-child {
        margin-right: 0;
      }
      @include tablet {
        display: block;
      }
    }
  }
  &__metro {
    display: flex;
    align-items: center;
    &-icon {
      margin-right: 10px;
    }
  }
  &__price-container {
    @include mobileL {
      max-width: 90px;
    }
  }
  &__price {
    font-size: 15px;
    color: $accent;
    font-weight: 700;
    margin-right: 10px;
    @include tabletL {
      font-size: 14px;
    }
  }
  &__bottom-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__date {
    margin-right: 10px;
  }
  &__btn {
    width: 40px;
    height: 40px;
    z-index: 3;
    margin-right: 15px;
    &:last-child {
      margin-right: 0;
    }
    @include mobileL {
      margin-right: 5px;
    }
    & svg {
      width: 15px;
      height: 15px;
      fill: $white;
      pointer-events: none;
    }
    &--delete {
      &:hover {
        border-color: $alert;
        background: $white;
        & svg {
          fill: $alert;
        }
      }
      
    }
  }
  &__edit-icon {
    @include tablet {
      width: 15px;
      height: 15px;
    }
  }
  &__client-info {
    background: $accent;
    padding: 10px;
    min-height: 80px;
    &-list {

    }
    &-item {
      font-size: 15px;
      color: $white;
      @include mobileL {
        font-size: 14px;
      }
    }
    &-link {
      color: $white;
    }
  }
}