.pagination {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  & li {
    transition: background-color .3s;
    &:hover:not(.active) {
      background-color: #ddd;
    }
  }
  & .active {
    background-color: $accent;
    & a {
      color: $white;
    }
  }
  & .disabled {
    &:hover {
      opacity: .5;
    }
  }
  & a, & span {
    padding: 10px 14px;
    display: block;
  }
  & a {
    color: $black;
    transition: color .3s;
  }
  &__btn {
    color: $white;
  }
}

.property-main .pagination a {
  color: $white;
}