.property {
  &-main {
    background: $second;
    padding: 20px;
  }
  &__cards {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
  }
  &__cards-block {
    position: relative;
    background: $white;
    margin-bottom: 20px;
    max-width: 1000px;
    width: 48%;
    &:hover {
      box-shadow: $shadow-down;
    }
    @include laptopM {
      width: 33%;
    }
    @include tabletL {
      width: 48%;
    }
    @include tablet {
      width: 100%;
    }
  }
}

.property-popup {
  display: none;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  z-index: 1111;
  padding: 10px;
  &--active {
    display: flex;
  }
  &__wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: $black;
    opacity: .5;
  }
  &__container {
    position: relative;
    background: $white;
    width: 50%;
    margin: 0 auto;
    border: 2px solid $black;
    @include tablet {
      width: 100%;
    }
    @include MHtabletM {
      height: 100%;
      overflow: auto;
    }
  }
  &__input {
    font-size: 16px;
    padding: 10px;
  }
  &__btn {
    position: fixed;
    bottom: 10%;
    right: 10px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    padding: 5px;
    line-height: 0;
    z-index: 2222;
    opacity: 1;
    background: $white;
    & svg {
      fill: $accent;
    }
    &:hover {
      background: $accent;
      & svg {
        fill: $white;
      }
    }
  }
  &__btn-close {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 24px;
  }
}